///////////////////////////////////////////////////           편지지           ///////////////////////////////////////////////////////
// 1번 편지지
.letterPaper1 {
    background-image: url("../../../assets/letters/Letter1.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    padding: 1.25rem;
    height: 52vh;
    // height: 100%;
    // width: 35vh;
    // width: 100%;
    // object-fit: contain;
    position: relative;
    display: flex;
  
    // @media screen and (max-width: 640px) {
    //   width: 30vh;
    // }
  }
  
  .letterContent1 {
    position: absolute;
    border: none;
    background-color: transparent;
    font-size: 1.5vh;
    resize: none;
    line-height: 1.765vh;
    height: 38.5vh;
    // height: 72%;
    width: 26vh;
    // width: 55%;
    justify-items: center;
    align-self: center;
    display: flex;
    overflow-y: hidden;
  
    &:focus {
      outline: 0cm;
    }
  }
  
